
import React from "react";
import { runForceGraph } from "./ForceGraphGenerator";
import styles from "./forceGraph.module.css";

// import React from "react";
// import rd3 from "react-d3-library";
// var node = document.createElement("div")
// d3.select("body")


export function ForceGraph({ linksData, nodesData, nodeHoverTooltip, mytemp }) {
  const containerRef = React.useRef("huurdur");
  
  // if (containerRef.current) {
  //   runForceGraph(containerRef.current, linksData, nodesData, nodeHoverTooltip);
  // }
  
  React.useEffect(() => {
    let destroyFn;

    if (containerRef.current) {
		
      const { destroy, update, nodes } = runForceGraph(containerRef.current, linksData, nodesData, nodeHoverTooltip);
      destroyFn = destroy;
      
      // console.log("yar", nodes())
      
      // console.log("up", update)
      
      mytemp(update)
    }

    return destroyFn;
  }, [nodesData]);

  return <div ref={containerRef} className={styles.container} />;
}