
import React, { Component } from "react";

import "bootstrap/dist/css/bootstrap.min.css";
import "./Components.css";

class Subjects extends Component {
	
	render() {
		const { myfunc, selected, nodesData } = this.props
		
		// console.log("subs", nodesData)
		
		let data = nodesData.sort((a, b) => a.code.localeCompare(b.code));
			
	    const idk = data.map((row, index) => {
			// console.log(row)
			const isSelected = selected.map((d) => d.id).includes(row.id);
			const text = isSelected ? "selected" : ""
			const bg = isSelected ? "80" : "100";
			
			const tdStyle = {
				// fontWeight : "bold",
				// color : row.color,
			}
			const trStyle = {
        		filter: "brightness(" + bg + "%)",
      		};
			
	      return (
			<>
	          <tr key={row.id} data-item={row.code} onClick={myfunc} style={trStyle}>
	            <td style={tdStyle}>{row.code}</td>
				<td>{row.semester}</td>
	            <td>{text}</td>
	          </tr>
			  {/* <hr/> */}
			</>
        );
	    });
		
		return (
      <table className="myTable">
        <thead>
          <tr>
            <th>code</th>
			<th>semester</th>
            <th>Index</th>
          </tr>
        </thead>
        <tbody>
          {/* <tr key={-1} data-item={-1} onClick={myfunc}>
            <td>{"all"}</td>
            <td>{-1}</td>
          </tr> */}
          {idk}
        </tbody>
      </table>
    );
	}
}

export default Subjects